import React from 'react';
import { Link } from 'react-router-dom';

const ThePost = ({index, context}) => {

    const posts = () => context.posts;
    const item = posts()[index];

    let linkPrefix = item.type === 'page' ? '/page/' : '/post/';
    let theContent = '';

    switch(context.route) {
        case '/':
        case '/search/:term':
        case '/category/:catid':
            theContent = item.excerpt.rendered;
            break;
        default:
            theContent = item.content.rendered;
            break;
    }

    return (
        <div id={'post-id-'+item.id} className={'post-item'}>
            <h2><Link to={linkPrefix+item.slug}>{item.title.rendered}</Link></h2>
            {/* <PostMeta index={index}></PostMeta> */}
            <div className="post-content" setInnerHTML={{__html:theContent}}></div>
        </div>
    )
}

export default ThePost;